import React from 'react'
import styled from 'styled-components'
import { ReactComponent as CloseIcon } from '../../images/close.svg'
import { ReactComponent as CheckIcon } from '../..//images/check.svg'

const ConfirmRemove = ({ name, onConfirm, onCancel }) => {
  return (
    <Box>
      <Text>
        Вы действительно хотите удалить <Text bold> {name}</Text>?
      </Text>

      <Flex>
        <Button color="successColor" onClick={onConfirm}>
          <CheckIcon fill="#ffffff" />
        </Button>
        <Button color="warningColor" onClick={onCancel}>
          <CloseIcon fill="#ffffff" />
        </Button>
      </Flex>
    </Box>
  )
}

export default ConfirmRemove

const Box = styled.div`
  width: 320px;
  padding: 20px;
  border: ${props => props.theme.border};
  border-radius: 4px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.55);
  text-align: center;
  background-color: #fff;
`
const Text = styled.span`
  display: inline-block;
  font-size: 24px;
  color: ${props => props.theme.textColorLight};
  margin-bottom: ${props => (props.bold ? 0 : '24px')};
  font-weight: ${props => (props.bold ? 700 : 400)};
`
const Flex = styled.div`
  display: flex;
  justify-content: space-around;
`
export const Button = styled.button`
  display: block;
  width: 50px;
  height: 50px;
  color: ${prop => prop.theme[prop.color]};
  cursor: pointer;
  font-size: 18px;
  font-weight: 400;
  transition: font-weight 200ms linear;
  border-radius: 50%;
  border: none;
  background: ${prop => prop.theme[prop.color]};
  &:hover {
    font-weight: 700;
    transition: font-weight 200ms linear;
  }
`
