import React, { useEffect, useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Formik, Form } from 'formik'
import * as Yup from 'yup'
import styled from 'styled-components'
import { ButtonRound, ErrorText } from '../stylesheets/mainStyled'
import { device } from '../stylesheets/device'
import { ReactComponent as CloseIcon } from '../images/close.svg'
import { ReactComponent as CheckIcon } from '../images/check.svg'
import TextInput from './FormikInputs/TextInput'
import MaskInput from './FormikInputs/MaskInput'
import SelectInput from './FormikInputs/SelectInput'
import { getMemberId } from '../redux/staff/staffOperations'
import { getError, getLoader, getMember } from '../redux/staff/staffSelectors'
import Loading from './Loader'
import { getAuth } from '../redux/global/globalSelectors'
import { editMemberId, staffError, staffLoader } from '../redux/staff/staffActions'
import { editMember } from '../services/apiMethods'

const EditMember = ({ onClose, id }) => {
  const dispatch = useDispatch()
  const auth = useSelector(getAuth)
  const member = useSelector(getMember)
  const loader = useSelector(getLoader)
  const error = useSelector(getError)

  const getMemberInfo = useCallback(id => dispatch(getMemberId(id)), [dispatch])
  useEffect(() => {
    getMemberInfo(id)
  }, [getMemberInfo, id])

  return (
    <Container>
      <Header>
        <Title>Сотрудник</Title>
        <CloseIcon onClick={onClose} />
      </Header>
      {member && (
        <Formik
          initialValues={member}
          validationSchema={Yup.object({
            surname: Yup.string().required('is required'),
            name: Yup.string().required('is required'),
            phone: Yup.string()
              .test(
                'len',
                'не коректный номер телефона',
                val => val?.replace(/[^0-9]/g, '').length === 12
              )
              .required('is required'),
            memberType: Yup.string().required('is required'),
          })}
          onSubmit={async ({ surname, name, phone, memberType }) => {
            const member = {
              id,
              surname,
              name,
              phone: phone.replace(/[^0-9]/g, ''),
              memberType,
            }
            dispatch(staffLoader())
            try {
              const { data } = await editMember(member, auth)
              dispatch(editMemberId(data))
              onClose()
            } catch (error) {
              dispatch(staffError(error))
            }
          }}
        >
          <Form>
            <FormBox>
              <TextInput label="Фамилия" name="surname" type="text" autoComplete="none" />
              <TextInput label="Имя" name="name" type="text" autoComplete="none" />
              <MaskInput
                label="Номер телефона"
                name="phone"
                type="text"
                mask="+38(999) 999-99-99"
              />
              <SelectInput label="Должность" name="memberType">
                <option value={member.memberType}>{member.memberType}</option>
                <option
                  value="DRIVER"
                  hidden={member.memberType === 'DRIVER' ? true : false}
                >
                  DRIVER
                </option>
                <option
                  value="STEWARD"
                  hidden={member.memberType === 'STEWARD' ? true : false}
                >
                  STEWARD
                </option>
                <option
                  value="DISPATCHER"
                  hidden={member.memberType === 'DISPATCHER' ? true : false}
                >
                  DISPATCHER
                </option>
              </SelectInput>
              {error && <ErrorText>error</ErrorText>}
              <ButtonRound type="submit">
                {loader ? (
                  <Loading type="ThreeDots" color="#00BFFF" height={15} width={40} />
                ) : (
                  <CheckIcon />
                )}
              </ButtonRound>
            </FormBox>
          </Form>
        </Formik>
      )}
    </Container>
  )
}

export default EditMember

const Container = styled.div`
  background-color: #fff;
  height: 100%;
  width: 100%;
  position: relative;

  @media ${device.tablet} {
    width: 320px;
    margin: 0 auto;
  }
`

const Header = styled.div`
  padding: 12.4px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: ${prop => prop.theme.mainColor};
`
const Title = styled.p`
  font-size: 24px;
`
const FormBox = styled.div`
  padding: 20px;
`
