import React, { useState, useEffect, useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import styled from 'styled-components'
import { ReactComponent as DeleteIcon } from '../images/delete.svg'
import { ReactComponent as PhoneIcon } from '../images/phone.svg'
import AddMember from '../components/AddMember'
import ButtonAdd from '../components/ButtonAdd'
import { device } from '../stylesheets/device'
import EditMember from '../components/EditMember'
import ConfirmRemove from '../components/ModalWindows/ConfirmRemove'
import Modal from '../components/Modal'
import { getError, getStaff } from '../redux/staff/staffSelectors'
import { getAllStaff } from '../redux/staff/staffOperations'
import { removeMemberId, setMember, staffError } from '../redux/staff/staffActions'
import { removeMember } from '../services/apiMethods'
import { getAuth } from '../redux/global/globalSelectors'
import { ErrorText } from '../stylesheets/mainStyled'

const StaffPage = () => {
  const dispatch = useDispatch()
  const staff = useSelector(getStaff)
  const error = useSelector(getError)
  const [isAdd, setIsAdd] = useState(false)

  const getStaffInfo = useCallback(() => dispatch(getAllStaff()), [dispatch])
  useEffect(() => {
    getStaffInfo()
  }, [getStaffInfo])

  const handleCloseModalAd = () => {
    setIsAdd(false)
    dispatch(staffError(null))
  }
  const handleOpenModalAdd = () => setIsAdd(true)

  if (error) return <ErrorText>{error.message}</ErrorText>

  return (
    <Container>
      {staff.length > 0 ? (
        staff.map(member => <Member key={member.id} member={member} />)
      ) : (
        <Text>Добавьте сотрудников</Text>
      )}

      <ButtonAdd click={handleOpenModalAdd} />
      <Modal open={isAdd} onClose={handleCloseModalAd}>
        <AddMember onClose={handleCloseModalAd} />
      </Modal>
    </Container>
  )
}

export default StaffPage

const Member = ({ member }) => {
  const dispatch = useDispatch()
  const auth = useSelector(getAuth)

  const [isEdit, setIsEdit] = useState(false)
  const [isRemove, setIsRemove] = useState(false)

  const handleOpenEdit = () => setIsEdit(!isEdit)
  const handleCloseEdit = () => {
    setIsEdit(false)
    dispatch(setMember(null))
    dispatch(staffError(null))
  }

  const toggleOpenModalRemove = () => setIsRemove(!isRemove)
  const handleConfirmRemove = async () => {
    try {
      const { status } = await removeMember(member.id, auth)
      if (status === 200) {
        setIsRemove(false)
        dispatch(removeMemberId(member.id))
      }
    } catch (error) {
      dispatch(staffError(error))
    }
  }
  return (
    <>
      <Card>
        <FlexRow>
          <LinkBox href={`tel: ${member.phone}`}>
            <PhoneIcon />
          </LinkBox>
          <FlexColumn onClick={handleOpenEdit}>
            <Text bold>
              {member.surname} {member.name}
            </Text>
            <Text>{member.phone}</Text>
            <Text bold>{member.memberType}</Text>
          </FlexColumn>
        </FlexRow>
        <DeleteIcon onClick={toggleOpenModalRemove} />
      </Card>
      {/* modal edit */}
      <Modal open={isEdit} onClose={handleOpenEdit}>
        <EditMember onClose={handleCloseEdit} id={member.id} />
      </Modal>
      {/* modal remove */}
      <Modal open={isRemove} onClose={toggleOpenModalRemove}>
        <ConfirmRemove
          name={`${member.surname} ${member.name}`}
          onConfirm={handleConfirmRemove}
          onCancel={toggleOpenModalRemove}
        />
      </Modal>
    </>
  )
}

const Container = styled.div`
  padding: 20px;
  @media ${device.tablet} {
    display: flex;
    flex-wrap: wrap;
  }
`
const Card = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px 20px 0;
  margin-bottom: 20px;
  border-bottom: ${prop => prop.theme.border};
  &:last-child {
    margin-bottom: 0;
  }
  @media ${device.tablet} {
    flex-direction: column;
    border: ${prop => prop.theme.border};
    border-radius: 4px;
    padding: 12px;
    margin: 0 20px 20px 0;
    background-color: rgba(0, 0, 0, 0.1);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1);
  }
`
const FlexRow = styled.div`
  display: flex;
  align-items: center;
  @media ${device.tablet} {
    flex-direction: column;
  }
`
const LinkBox = styled.a`
  padding: 12px;
  @media ${device.tablet} {
    padding: 0 40px;
    margin-bottom: 12px;
  }
`
const FlexColumn = styled.div`
  padding-left: 20px;
  cursor: pointer;

  @media ${device.tablet} {
    margin-bottom: 12px;
  }
`
const Text = styled.p`
  font-size: 18px;
  font-weight: ${prop => (prop.bold ? 700 : 400)};
  margin-bottom: 6px;
  text-decoration: underline;
`
