import axios from 'axios'

import * as globalSelectors from '../global/globalSelectors'
import { setMember, setStaff, staffError, staffLoader } from './staffActions'

axios.defaults.baseURL = 'https://car.busis.eu'

export const getAllStaff = () => async (dispatch, getState) => {
  const auth = globalSelectors.getAuth(getState())
  if (!auth) return
  dispatch(staffLoader())
  try {
    const { data } = await axios('/api/employee/all', {
      headers: { Authorization: auth },
    })
    dispatch(setStaff(data))
  } catch (error) {
    dispatch(staffError(error))
  }
}

export const getMemberId = id => async (dispatch, getState) => {
  const auth = globalSelectors.getAuth(getState())
  if (!auth) return
  dispatch(staffLoader())
  try {
    const { data } = await axios(`/api/employee?employeeId=${id}`, {
      headers: { Authorization: auth },
    })
    dispatch(setMember(data))
  } catch (error) {
    dispatch(staffError(error))
  }
}
